import React from 'react';
import { connect } from 'react-redux';

function Alert(props){
    
    let closeBtn = (()=>{
        if(props.alertOpen === true){
            props.dispatch({type:false});
        }
    })

    return(
        <div className="my-alert my-alert-white-yellow">
            <p>{props.information}</p>
            {props.type === 'type2' ? (<button onClick={closeBtn}>닫기</button>) : null}
        </div>
    )
}

function storeFuc(state){
    return {
        alertOpen : state.reducer2
    }
}

export default connect(storeFuc)(Alert)
