import React, { useContext } from 'react';
import { Table } from 'react-bootstrap';
import { connect } from 'react-redux';
import Alert from '../Common/Alert';

function Cart(props){


    return (
    <div>
        <Table responsive="sm">
            <thead>
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>수량</th>
                    <th>변경</th>
                </tr>
            </thead>
            <tbody>
                {
                props.state.map((a,i)=>{
                    return (
                    <tr key = { i }>
                        <td>{ a.id }</td>
                        <td>{ a.name }</td>
                        <td>{ a.quan }</td>
                        <td><button onClick={()=>{
                            props.dispatch({ type : 'quanIncrease',index : a.id})
                        }}>+</button>
                        <button onClick={()=>{
                            props.dispatch({type : 'quanDecrease',index : a.id})
                        }}>-</button>
                        </td>
                    </tr>
                    )
                })
                
                }

            </tbody>
        </Table>
        {
        props.alertOpen === true 
        ? (<Alert information={'지금 구매하시면 신규할인 20%'} type="type2" alertOpen={props.alertOpen}/>) : null }
    </div>
    );
}

function storeFuc(state){
    return {
        state : state.reducer,
        alertOpen : state.reducer2
    }
}

export default connect(storeFuc)(Cart)